<template>
    <section class="pc-container pb-4">
        <div class="pcoded-content">
            <div class="row mb-4">
                <h5 class="text-dark fw-bold">Equipo UNPRG</h5>
            </div>
            <div class="row">
                <div class="col-xl-12">
                    <div class="card shadow">
                        <div class="card-header">
                            <div class="d-flex align-items-center">
                                <h5 class="text-secondary">Lista de Abogados</h5>
                                <button @click="mostrarModal" class="btn btn-primary btn-lg ml-auto rounded">
                                    Registrar Nuevo Abogado
                                </button>
                            </div>
                        </div>
                        <div class="card-body">
                            <div class="table-responsive">
                                <table class="table" id="table" ref="myDataTable">
                                    <thead>
                                        <tr>
                                            <th>Documento</th>
                                            <th>Nombres</th>
                                            <th>Apellidos</th>
                                            <th>Telefono</th>
                                            <th>Email</th>
                                            <th>Opciones</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr v-for="(abogado, index) in abogados" :key="index">
                                            <td>{{ abogado.nat_dni }}</td>
                                            <td>{{ abogado.nat_nombres }}</td>
                                            <td>{{ nombreCompleto(abogado) }}</td>
                                            <td>{{ abogado.nat_telefono }}</td>
                                            <td>{{ abogado.nat_correo }}</td>
                                            <td>
                                                <a href="javascript:void(0)" @click="editar(abogado.abo_id)"
                                                    class="text-primary px-2 py-1 btn-lg mx-1">
                                                    <span class=""><i data-feather="edit"></i></span>
                                                </a>
                                                <a href="javascript:void(0)" @click="eliminar(abogado.abo_id)"
                                                    class="px-2 py-1 btn-lg text-danger">
                                                    <span class=""><i data-feather="trash"></i></span>
                                                </a>
                                            </td>

                                        </tr>
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div v-if="showModal" class="modal" @click="cerrarModal">
                <div class="modal-content card border-0" @click.stop>
                    <div class="card-header pt-3 pb-4">
                        <h5 class="text-center fw-bold" style="margin-top: 10px !important;">
                            {{ abogado.abo_id ? 'Editar Abogado' : 'Registrar Nuevo Abogado' }}</h5>
                    </div>
                    <div class="card-body">
                        <div class="row">
                            <div class="col-md-6">
                                <div class="form-group">
                                    <label class="form-label">DNI</label>
                                    <input v-model="abogado.nat_dni" class="form-control" @input="errores.nat_dni = false"
                                        @blur="validarDni" :class="{ 'has-error': errores.nat_dni }" />
                                    <span class="error-message" v-if="errores.nat_dni">Complete este campo para
                                        continuar.</span>
                                </div>
                            </div>
                            <div class="col-md-6">
                                <div class="form-group">
                                    <label class="form-label">Apellido Paterno</label>
                                    <input v-model="abogado.nat_apellido_paterno" class="form-control"
                                        @input="errores.nat_apellido_paterno = false" @blur="validarAPaterno"
                                        :class="{ 'has-error': errores.nat_apellido_paterno }" />
                                    <span class="error-message" v-if="errores.nat_apellido_paterno">Complete este campo para
                                        continuar.</span>
                                </div>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col-md-6">
                                <div class="form-group">
                                    <label class="form-label">Apellido Materno</label>
                                    <input v-model="abogado.nat_apellido_materno" class="form-control"
                                        @input="errores.nat_apellido_materno = false" @blur="validarAMaterno"
                                        :class="{ 'has-error': errores.nat_apellido_materno }" />
                                    <span class="error-message" v-if="errores.nat_apellido_materno">Complete este campo para
                                        continuar.</span>
                                </div>
                            </div>
                            <div class="col-md-6">
                                <div class="form-group">
                                    <label class="form-label">Nombres</label>
                                    <input v-model="abogado.nat_nombres" class="form-control"
                                        @input="errores.nat_nombres = false" @blur="validarNombres"
                                        :class="{ 'has-error': errores.nat_nombres }" />
                                    <span class="error-message" v-if="errores.nat_nombres">Complete este campo para
                                        continuar.</span>
                                </div>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col-md-6">
                                <div class="form-group">
                                    <label class="form-label">Email</label>
                                    <input v-model="abogado.nat_correo" class="form-control" />
                                </div>
                            </div>
                            <div class="col-md-6">
                                <div class="form-group">
                                    <label class="form-label">Telefono</label>
                                    <input v-model="abogado.nat_telefono" class="form-control" />
                                </div>
                            </div>
                        </div>
                        <div class="row">
                            <div class="d-flex justify-content-end">
                                <button class="btn btn-secondary btn-lg mx-2 px-4 rounded"
                                    @click="cerrarModal">Cerrar</button>
                                <button class="btn btn-primary btn-lg rounded px-4" @click="guardar">
                                    {{ abogado.abo_id ? 'Actualizar' : 'Guardar' }}
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>
  
<script>
import { mapActions } from "vuex";
import feather from 'feather-icons';
import DataTable from '../../utils/dataTable';

export default {
    name: "abogado",
    mixins: [DataTable],
    mounted() {
        this.leer();
    },
    updated() {
        feather.replace();
    },
    data() {
        return {
            abogados: [],
            modalVisible: false,
            dataTable: null,
            showModal: false,
            abogado: {
                nat_dni: "",
                nat_apellido_paterno: "",
                nat_apellido_materno: "",
                nat_nombres: "",
                nat_telefono: "",
                nat_correo: "",
            },
            errores: {
                nat_dni: "",
                nat_apellido_paterno: "",
                nat_apellido_materno: "",
                nat_nombres: "",
                nat_telefono: "",
                nat_correo: "",
            }
        };
    },
    methods: {
        ...mapActions(["get", "post"]),

        limpiarCampos() {
            for (let key in this.abogado) {
                if (this.abogado.hasOwnProperty(key)) {
                    this.abogado[key] = "";
                }
            }
            console.log('campos limpiados');
        },

        limpiarErrores() {
            for (let key in this.errores) {
                if (this.errores.hasOwnProperty(key)) {
                    this.errores[key] = "";
                }
            }
            console.log("errores limpiados");
        },

        mostrarModal() {
            this.showModal = true;
        },

        cerrarModal() {
            this.showModal = false;
            this.limpiarCampos();
            this.limpiarErrores();
        },

        nombreCompleto(abogado) {
            return `${abogado.nat_apellido_paterno} ${abogado.nat_apellido_materno}`;
        },

        validarCampo(campo, propiedadError) {
            if (this.abogado[campo] === "") {
                this.errores[propiedadError] = true;
            } else {
                this.errores[propiedadError] = false;
            }
        },

        validarDni() {
            this.validarCampo('nat_dni', 'nat_dni');
        },
        validarAPaterno() {
            this.validarCampo('nat_apellido_paterno', 'nat_apellido_paterno');
        },
        validarAMaterno() {
            this.validarCampo('nat_apellido_materno', 'nat_apellido_materno');
        },
        validarNombres() {
            this.validarCampo('nat_nombres', 'nat_nombres');
        },
        validarCampos() {
            this.validarDni(),
                this.validarAPaterno(),
                this.validarAMaterno(),
                this.validarNombres()

            return !Object.values(this.errores).some(error => error);
        },

        async crear() {
            if (this.validarCampos()) {
                try {
                    const response = await this.post({
                        url: `${this.$store.getters.get__url}/lawyer/registrar`,
                        token: this.$store.getters.get__token,
                        params: this.abogado,
                    });
                    if (response.state === 0) {
                        this.showModal = false;
                        this.limpiarCampos();
                        this.leer();

                    } else {
                        console.error("Error en la respuesta del servidor:", response);
                    }
                } catch (error) {
                    console.error("Error en la solicitud:", error);
                }
            } else {
                console.log('Error en la validación de campos');
            }
        },
        async leer() {
            try {
                this.$showLoadingAlert();
                const result = await this.$getData('lawyer', this);
                this.abogados = result.data;
                await this.$waitForDataTableDraw(this, 'myDataTable');
                this.$closeLoadingAlert();
            } catch (error) {
                this.$handleApiError(error, 'leer abogados');
            }
        },

        async actualizar() {
            if (this.validarCampos()) {
                try {
                    const response = await this.post({
                        url: `${this.$store.getters.get__url}/lawyer/update`,
                        token: this.$store.getters.get__token,
                        params: this.abogado,
                    });
                    if (response.state === 0) {
                        this.limpiarCampos();
                        this.leer();
                        this.showModal = false;
                    } else {
                        console.error("Error en la respuesta del servidor:", response);
                    }
                } catch (error) {
                    console.error("Error en la solicitud:", error);
                }
            } else {
                console.log('Error en la validación de campos');
            }
        },

        async eliminar(id) {
            if (confirm("¿Estás seguro de que deseas eliminar a este abogado?")) {
                try {
                    const response = await this.post({
                        url: `${this.$store.getters.get__url}/lawyer/eliminar/${id}`,
                        token: this.$store.getters.get__token,
                    });

                    if (response.state === 0) {
                        const index = this.abogados.findIndex(abogado => abogado.abo_id === id);
                        if (index !== -1) {
                            this.abogados.splice(index, 1);
                        }
                    } else {
                        console.error("Error al eliminar el abogado.");
                    }
                } catch (error) {
                    console.error("Error al eliminar el abogado.", error);
                }
            }
        },

        // mostrar datos de la fila seleccionada sin llamar a la API
        editar(id) {
            try {
                const abogadoEdit = this.abogados.find(abogado => abogado.abo_id === id);
                if (abogadoEdit) {
                    this.abogado = { ...abogadoEdit };
                    this.showModal = true;
                } else {
                    console.error("Abogado no ENCONTRADO");
                }
            } catch (errores) {
                console.error(errores);
            }
        },

        guardar() {
            try {
                if (!this.abogado.abo_id) {
                    this.crear();
                    console.info('crear');
                } else {
                    this.actualizar(this.abogado.abo_id);
                    console.info('actualizar');
                }
            } catch (errores) {
                console.error(errores);
            }
        },

        actualizarTabla() {
            if (this.dataTable) {
                this.dataTable.destroy();
            }
            this.$nextTick(() => {
                this.initDataTable();
            });
        }

    },

};
</script>
  
<style scoped>
.has-error {
    border: 1px solid #EA4D4D;
}

.error-message {
    color: #EA4D4D;
    font-size: 12px;
}

td {
    white-space: normal !important;
    word-wrap: break-word;
}

.circle {
    width: 30px;
    height: 30px;
    background-color: #7267EF;
    color: white;
    font-weight: 600;
    border-radius: 50%;
    text-align: center;
    line-height: 30px;
}

.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
}

.modal-content {
    background: #fff;
    width: 600px;
    height: auto !important;
    /* padding: 20px; */
    border-radius: 8px !important;
    box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px, rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;
    position: relative !important;
}

.card-header {
    border-radius: 8px 8px 0px 0px !important;
}
</style>
  